.header * {
    color: hsl(189, 100%, 85%);
}

.btnAppearence {
    display: flex;

    width: 100%;
    height: 100%;

    justify-content: center;
    align-items: center;


    background-color: transparent;

    &:hover {
        background-color: rgba(2, 2, 37, 0.2);
        letter-spacing: 2px;
    }
}

button {
    background-color: transparent;
    border: none;

    &:hover {
        letter-spacing: 4px;
    }
}

.btn {
    letter-spacing: 4px;
    font-size: 1rem;

    &:hover {
        letter-spacing: 2px;
    }
}

svg {
    width: 40px;
    height: 40px;
}


/*
******* <<<<< INICIO
*/

.header {
    display: grid;
    position: fixed;

    grid-template-columns: 50px auto 50px;

    top: 0;
    width: 100%;

    align-items: center;

    background-color: rgba(0, 0, 59, 0.8);
    box-shadow: 2px 2px 4px rgba(0, 0, 59, 0.4);

    backdrop-filter: blur(2px);

    z-index: 10;

    .ICON {
        display: flex;
        align-items: center;
        width: 60px;
        height: 100%;

        margin: 0 10px;

        &>svg {
            transition: all 2s;
            height: 40px;
        }
    }
}

/*cel*/
@media only screen and (max-width: 667px) {
    .header {
        height: 60px;

        * {
            overflow: hidden;
        }

        ul {
            content: '';
            width: 0;
            height: 0;
        }
    }
}



.ulHeader {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    height: 100%;
    width: 100%;


    justify-content: space-around;

    li {
        width: 8em;
        height: 40px;
        align-self: center;
        margin: 2px;

        background-color: hsla(230, 100%, 90%, 0.04);
        border: thick solid hsla(180, 100%, 60%, 0.02);
    }
}

.exp {
    letter-spacing: 2px;
    height: 40px;
    width: 100%;
}

.ExtraUl {
    display: grid;
    grid-template-columns: repeat(auto);

    li {
        height: 2em !important;
        width: 100%;

        background-color: rgba(2, 2, 37, 0.5);
    }
}

/*pc*/
@media only screen and (min-width: 667px) {
    .ulHeader {
        justify-content: flex-end;

        li {
            width: 10em;
            height: 60px;
        }
    }
}


/***********************SuperDiv***********************/

.superDiv {
    .unactive {
        button {
            position: relative;
            border: 4px gray;

            z-index: 0;

            &&::before {
                position: absolute;
                content: '';
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;

                border-radius: 50%;

                z-index: -1;
                box-shadow: 0 0 2px 4px rgb(0, 0, 0);
                opacity: .2;
            }
        }
    }

    .active {
        transform: translateY(-10px);

        button {
            position: relative;

            z-index: 0;

            &::before {
                position: absolute;
                content: '';
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;

                border-radius: 50%;

                z-index: -1;

                box-shadow: 0 0 2px 8px rgba(132, 0, 255, 0.4);
                opacity: .4;
            }
        }
    }
}

.superDiv {
    position: fixed;

    width: auto;
    height: auto;

    min-width: 40%;


    bottom: 0;

    z-index: 20;
    opacity: .8;

    &>nav {
        width: 100%;
        height: auto;

        background-color: rgba(127, 255, 212, 0.2);
        border-radius: 10px;

        &>ul {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;

            width: 100%;
            height: 100%;

            border-radius: 10px;

            &>li {
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;

                background-color: rgba(13, 106, 106, 0.8);
                margin: 0 10px;

                border-radius: 20%;

                &>div:first-child>button {
                    background-color: rgb(60, 255, 0);
                    transform: translateY(-15px);
                }

                &>div {
                    margin: 2px 6px;


                    &>button {
                        width: 40px;
                        height: 40px;

                        border-radius: 50%;


                        background-color: rgb(122, 252, 252);

                        transform: translateY(-5px);

                        svg {
                            width: 25px;
                            height: 25px;
                        }
                    }
                }

            }
        }
    }
}

/*pc*/
@media only screen and (min-width: 667px) {
    .superDiv {
        ul {
            li {
                div {
                    margin: 2px 6px;

                    &>button {
                        width: 55px;
                        height: 55px;

                        svg {
                            width: 50px;
                            height: 50px;
                        }
                    }
                }
            }
        }
    }
}

/*cel*/
@media only screen and (max-width: 667px) {
    .superDiv {
        ul {
            justify-content: flex-start !important;

            li {
                margin: 10px 0 !important;

                &:first-child {
                    width: 100% !important;
                }
            }
        }
    }
}


/*********************** SuperItem ***********************/

.SuperItem {
    position: relative;

    display: grid;
    grid-template-columns: auto 2px;
    * {
        letter-spacing: 1px;
    }

    div {
        position: absolute;            
        
        top: 50%;
        left: 50%;

        width: max-content;
        
        transform: translate(-50%, -50%);
        
        transition: 
        transform 2s linear,
        opacity 600ms linear;

        &>abbr{
            display: block;
            text-align: center;

            background-color: rgba(0, 255, 255, 0.4);
        }
    }

    &.active{
        abbr {
            transform: translateY(250%);
            opacity: 1;
           }
    }
    &.unactive {
        abbr {
            transform: translateY(0px);
            opacity: 0;
           }
    }
}