* {
  scroll-behavior: smooth;
}

/* general */

section{
  background-color: hsla(180, 100%, 50%, 0.1);

  height: auto;
  min-height: 300px;

  &>article{
    min-height: 200px;
  
    margin: 20px 0;
  
    padding: 20px 5px;
  
    border: solid rgba(44, 138, 169);
    border-radius: 10px;
  
    background: rgba(12, 41, 100, 0.4);
    background: linear-gradient(90deg,
            rgb(3, 29, 73) 0%,
            rgba(6, 79, 142, 0.4) 50%,
            rgb(1, 29, 76) 100%);
  
    animation: shine 12s linear infinite;
    background-size: 200%;
  }
  
}

[class^="jump--"] {
  height: 200px;
  width: 100%;
  background: rgb(5, 64, 170);
  background: linear-gradient(0deg,
      rgba(2, 26, 51, 0.2) 24%,
      rgba(8, 138, 208, 0.4) 47%,
      rgba(2, 26, 51, 0.2) 67%);

  opacity: .2;
}


@keyframes shine {
  0% {
      background-position: -100% center;
  }

  100% {
      background-position: 100% center;
  }
}




/* EXTRAS */

* {
  margin: 0;
  padding: 0;
}

html {
  position: absolute;
  background: rgb(24, 15, 114);
  background: radial-gradient(circle,
      rgb(47, 106, 133) 0%,
      rgba(13, 45, 92, 1) 30%,
      rgb(11, 27, 47) 50%,
      rgb(23, 22, 38) 70%,
      rgb(9, 19, 38) 100%);

  width: 100%;
  height: 100%;

  scrollbar-color: cyan rgb(152, 175, 175);
}

ul,
li {
  list-style: none;
}

.App {
  display: flex;
  flex-direction: column;

  justify-content: center;
  align-items: center;

  overflow-x: hidden;

  &>div {
    padding: 20px 5px 40px 10px;
  }
}

.iButton {
  border-radius: 50%;
  width: 50px;
  height: 50px;
}